.nab-export-modal {
	width: 100%;
	max-width: 45em;

	&__actions {
		display: flex;
		justify-content: flex-end;
	}

	&__section {
		margin-bottom: 2em;

		&-name {
			font-size: 1.15em;
			font-weight: 700;
			margin-bottom: 0.5em;
		}

		&-options--checkboxes {
			display: grid;
			grid-template-columns: 1fr 1fr;
			gap: 0 1em;
		}

		&-options--selects {
			display: flex;
			gap: 0 1em;

			> * {
				flex: 1 1 0px;
			}
		}
	}
}
